.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #001347;
  height: 100px;
  padding: 10px;
  color: white;
  text-align: left;
}

.App-header p {
  margin: 5px 0px;
}

.App-title {
  font-size: 2.5em;
  margin: 5px 0px;
}

.add {
  font-size: large;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.plus {
  padding: 3px 14px;
  background-color: #cc0066;
  color: #fcb4d8;
  border-radius: 40px;
  -webkit-box-shadow: 1px 1px 5px 1px #7c7c7c;
          box-shadow: 1px 1px 5px 1px #7c7c7c;
  font-size: 240%;
  font-weight: bold;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



